import React from "react";
import { Col, Row } from "react-bootstrap";
import {
  DiJavascript1,
  DiReact,
  DiNodejs,
  DiPython,
  DiGit,
  DiDjango,
  DiBootstrap,
  DiJqueryLogo,
  DiHtml5,
  DiCss3,
  DiPostgresql,

} from "react-icons/di";

function TechIcon(props) {
  return (
    <Col xs={4} md={2} className="tech-icons">
      {props.icon}
      {
        props.link ?
          <a href={props.link} className="tech-name" target="_blank" rel="noreferrer">
            {props.name}
          </a>
          : <span className="tech-name" style={{ display: "block" }}>
            {props.name}
          </span>
      }
    </Col>
  )
}

function Techstack() {
  return (
    <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
      <TechIcon icon={<DiJavascript1 />} link="https://" name="JavaScript" />
      <TechIcon icon={<DiPython />} link="https://www.python.org" name="Python" />
      <TechIcon icon={<DiHtml5 />} link="https://developer.mozilla.org/en-US/docs/Glossary/HTML5" name="HTML5" />
      <TechIcon icon={<DiCss3 />} link="https://developer.mozilla.org/en-US/docs/Glossary/CSS" name="CSS" />
      <TechIcon icon={<DiJqueryLogo />} link="https://jquery.com" name="Jquery" />
      <TechIcon icon={<DiNodejs />} link="https://nodejs.org" name="NodeJs" />
      <TechIcon icon={<DiReact />} link="https://reactjs.org" name="ReactJs" />
      <TechIcon icon={<DiDjango />} link="https://djangoproject.com" name="Django" />
      <TechIcon icon={<DiBootstrap />} link="https://getbootstrap.com" name="Bootstrap" />
      <TechIcon icon={<DiPostgresql />} link="https://postgresql.org" name="Postgresql" />
      <TechIcon icon={<DiGit />} link="https://git-scm.com" name="Git" />
    </Row>
  );
}

export default Techstack;
