import React from "react";
import { Col, Row } from "react-bootstrap";
import {
  AiOutlineGithub,
  AiOutlineInstagram,
  AiOutlineLinkedin,
  AiOutlineMail,
  AiOutlinePhone,
  AiOutlineWhatsApp,
} from "react-icons/ai"

import {
  DiStackoverflow
} from "react-icons/di"
import { FaKaggle } from "react-icons/fa";

import {
  GitHub, LinkedIn, Email, Phone,
  Instagram, WhatsApp, StackOverflow,
  Kaggle
} from "./links"



function SocialIcon(props) {
  return (
    <Col as='a' xs={4} md={2} className="social" target="_blank" rel="noreferrer" href={props.link}>
      <span className="social-icon" style={{ display: "block", color:props.color||'whitesmoke' }}>
        {props.icon}
      </span>
      <h4 className="social-name">{props.name}</h4>
      {
        props.info &&
        <p className="social-icon-info">
          {props.info}
        </p>
      }
    </Col>
  )
}


export default function SocialStack(props) {
  return (
    <Row id="social-stack" className="d-flex flex-wrap justify-content-between" style={{ justifyContent: "center", paddingBottom: 50, paddingTop:50 }}>
      <SocialIcon icon={<AiOutlinePhone/>} link={"tel:"+Phone} name="Phone"/>
      <SocialIcon icon={<AiOutlineMail/>} link={"mailto:"+Email} name="Email"/>
      <SocialIcon icon={<AiOutlineGithub/>} link={GitHub} name="GitHub"/>
      <SocialIcon icon={<AiOutlineInstagram/>} link={Instagram} name="Instagram" color="pink"/>
      <SocialIcon icon={<AiOutlineWhatsApp/>} link={WhatsApp} name="WhatsApp" color="green"/>
      <SocialIcon icon={<AiOutlineLinkedin/>} link={LinkedIn} name="LinkedIn" color="lightblue"/>
      <SocialIcon icon={<DiStackoverflow/>} link={StackOverflow} name="StackOverflow" color="orange"/>
      <SocialIcon icon={<FaKaggle/>} link={Kaggle} name="Kaggle" color="lightblue"/>
    </Row>
  )
}