import React from "react";
import { Col, Row } from "react-bootstrap";
import {
  SiLinux,
  SiVisualstudiocode,
  SiJupyter,
  SiHeroku,
  SiCodepen,
  SiCodecademy,
  SiSublimetext
} from "react-icons/si";

function TechIcon(props){
  return (
    <Col xs={4} md={2} className="tech-icons">
      {props.icon}
      {
        props.link?
        <a href={props.link} className="tech-name" target="_blank" rel="noreferrer">
          {props.name}
        </a>
        : <span className="tech-name" style={{display: 'block'}}>
          {props.name}
        </span>
      }
    </Col>
  )
}

function Toolstack() {
  return (
    <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
      <TechIcon icon={<SiLinux />} link="https://linux.org" name="Linux"/>
      <TechIcon icon={<SiVisualstudiocode />} link="https://code.visualstudio.com" name="VisualStudioCode" />
      <TechIcon icon={<SiJupyter />} link="https://jupyter.org" name="Jupyter Notebooks" />
      <TechIcon icon={<SiHeroku />} link="https://heroku.com" name="Heroku" />
      <TechIcon icon={<SiCodepen />} link="https://codepen.io" name="CodePen" />
      <TechIcon icon={<SiCodecademy />} link="https://codecademy.com" name="CodeCademy" />
      <TechIcon icon={<SiSublimetext />} link="https://sublimetext.com" name="Sublime Text" />
    </Row>
  );
}

export default Toolstack;